import React from 'react';
import { Button } from 'react-bootstrap';
import { connectRedux } from '../common/redux';
import createClass from 'create-react-class';

const NewGame = createClass({
    getInitialState() {
        return {
            name: '',
        };
    },

    changeName(name) {
        this.setState({name});
    },

    render() {
        return (
            <div className='text-center'>
                <div className="row top" />
                <h1>
          New Game
                </h1>
                <label>Name</label>
                <input className="form-control" value={this.state.name} onChange={e => this.changeName(e.target.value)} />
                <div className="row">
                    <Button onClick={() => this.props.changeRoute('/level-select')} bsSize='lg' bsStyle='primary'>Start Game</Button>
                </div>
                <div className="row">
                    <Button onClick={() => this.props.changeRoute('/')} bsSize='lg' bsStyle='primary'>Cancel</Button>
                </div>
            </div>
        );
    },
});

export default connectRedux(({files}) => ({files}))(NewGame);
