import React from 'react';
import { connectRedux } from '../../common/redux';

import Layer from './Layer';

const MouseTile = connectRedux(({board}) => ({board}))(({tile, boardMouseClick, board}) => {
    if(!tile.exists) {
        return null;
    }
    return (
        <rect x={tile.x * 46} y={tile.y * 46} width="40" height="40" className='mouse-tile' onClick={() => boardMouseClick(tile.x, tile.y)}/>
    );
});

const MouseLayer = ({board}) => {
    return (
        <Layer index={4}>
            {board.tiles.map((tile, i) => <MouseTile key={i} tile={tile} />)}
        </Layer>
    );
}

export default connectRedux(({board}) => ({board}))(MouseLayer);
