import _ from 'lodash';
import allAttacks from './attacks';

const baseInfo = {
    player_unit1: {
        image: 'r',
        tiles: [],
        color: 'orange',
        name: 'the_unit',
        maxLength: 5,
        moves: 4,
        attacks: ['attack1', 'attack2'],
    },
    cpu_unit1: {
        image: 'Z',
        tiles: [],
        color: 'lightblue',
        name: 'the_bad_guy',
        maxLength: 6,
        moves: 8,
        attacks: ['attack2'],
    },
};

function tileDistance(tile1, tile2) {
    return Math.abs(tile1[0] - tile2[0]) + Math.abs(tile1[1] - tile2[1]);
};

class Unit {
    constructor(type, tiles) {
        let attrs = baseInfo[type];
        if(!attrs) {
            console.warn('cannot find unit type ' + type);
        }
        _.merge(this, attrs);
        this.tiles = tiles;
        this.startTurn();
    }

    getAttacks() {
        return this.attacks.map(atk => allAttacks[atk]);
    }

    head() {
        return this.tiles[0];
    }

    isOn(tile) {
        return _.some(this.tiles, unitTile => _.isEqual(unitTile, tile));
    }

    removeTiles(amount) {
        if(amount <= 0) return;
        this.tiles = _.take(this.tiles, this.tiles.length - amount);
    }

    canMoveTo(tile) {
        return tileDistance(this.head(), tile) === 1;
    }

    moveTo(tile) {
        let newTiles = _.reject(this.tiles, unitTile => _.isEqual(unitTile, tile));
        newTiles.unshift(tile);
        this.tiles = newTiles;
        this.removeTiles(this.tiles.length - this.maxLength);
        this.movesTaken += 1;
    }

    canAttack(tile, attack) {
        // todo
        return this.canMoveTo(tile);
    }

    performAttack(attack, otherUnit) {
        if(attack.type === 'standard') {
            otherUnit.removeTiles(attack.damage);
        }
        this.endTurn();
    }

    hasMovementLeft() {
        return this.movesTaken < this.moves && !this.finished;
    }

    startTurn() {
        this.movesTaken = 0;
        this.finished = false;
        this.startingTiles = _.cloneDeep(this.tiles);
    }

    endTurn() {
        this.finished = true;
    }

    resetMovement() {
        this.movesTaken = 0;
        this.tiles = _.cloneDeep(this.startingTiles);
    }

}

export default Unit;
