import _ from 'lodash';
import Unit from '../lib/Unit';

const rows = _.range(0, 15);
const cols = _.range(0, 10);

let tiles = _.flatMap(cols, i => rows.map(j => (
    {
        x: j,
        y: i,
        exists: true,
    }
)));

_.find(tiles, tile => tile.x === 1 && tile.y === 4).exists = false;
_.find(tiles, tile => tile.x === 2 && tile.y === 5).exists = false;
_.find(tiles, tile => tile.x === 4 && tile.y === 4).exists = false;
_.find(tiles, tile => tile.x === 6 && tile.y === 4).exists = false;


const DEFAULT_STATE = {
    selectedUnit: null,
    selectedAttack: null,
    tiles: tiles,
    playerUnits: [
        new Unit('player_unit1', [[1,2], [2,2], [2,3], [2,4]]),
    ],
    cpuUnits: [
        new Unit('cpu_unit1', [[6,2], [7,2], [7,3], [7,4]]),
    ],
    currentTurn: 'playerUnits',
};

_.forEach(DEFAULT_STATE.cpuUnits, unit => unit.endTurn());

function isAnyOtherUnitOn({playerUnits, cpuUnits, selectedUnit}, tile) {
    let tiles = _(playerUnits).concat(cpuUnits).reject(unit => unit === selectedUnit).flatMap('tiles').value();
    return _.some(tiles, t => _.isEqual(t, tile));
}

function checkOtherStatuses(state) {
    if(_.every(state[state.currentTurn], 'finished')) {
        console.log('finished', state.currentTurn);
        let nextTeam = state.currentTurn === 'playerUnits' ? 'cpuUnits' : 'playerUnits';
        _.forEach(state[nextTeam], unit => unit.startTurn());
        state.currentTurn = nextTeam;
    }
    return {
        ...state,
        playerUnits: _.filter(state.playerUnits, unit => unit.tiles.length > 0),
        cpuUnits: _.filter(state.cpuUnits, unit => unit.tiles.length > 0),
    };
}

export default (state = DEFAULT_STATE, action = {}) => {
    if(action.type === 'BOARD_MOUSE_CLICK') {
        let clickedTile = [action.x, action.y];
        let clickedUnit = _.find(_.concat(state.playerUnits, state.cpuUnits), unit => unit.isOn(clickedTile));
        if(state.selectedAttack && !state.selectedUnit.finished && state.selectedUnit.canAttack(clickedTile, state.selectedAttack)) { // attack mode
            if(clickedUnit) {
                state.selectedUnit.performAttack(state.selectedAttack, clickedUnit);
            } else {
                state.selectedUnit.endTurn();
            }
            return checkOtherStatuses({...state, selectedUnit: null, selectedAttack: null});
        } else { // movement mode
            if(state.selectedUnit && state.selectedUnit.hasMovementLeft() && (!clickedUnit || clickedUnit === state.selectedUnit)) {
                let boardTile = _.find(state.tiles, {x: action.x, y: action.y});
                if(boardTile.exists && !isAnyOtherUnitOn(state, clickedTile) && state.selectedUnit.canMoveTo(clickedTile)) {
                    state.selectedUnit.moveTo(clickedTile);
                    return checkOtherStatuses({...state});
                }
            }
            return checkOtherStatuses({...state, selectedUnit: clickedUnit, selectedAttack: null});
        }

    } else if(action.type === 'SELECT_ATTACK') {
        return checkOtherStatuses({...state, selectedAttack: action.attack});
    } else if(action.type === 'UNDO_MOVEMENT') {
        if(state.selectedUnit) {
            state.selectedUnit.resetMovement()
        }
        return checkOtherStatuses({...state, selectedAttack: null});
    }
    return state;
};
