import React from 'react';
import { Button } from 'react-bootstrap';
import { connectRedux } from '../common/redux';
import _ from 'lodash';

const LevelSelect = (props) => {
    const levels = _.range(1, 10);
    return (
        <div>
            <div className="row top" />
            <div className="row">
                <div className="col-md-6">
                    <h1>Choose Level</h1>
                    {levels.map(n => <div key={n} className="row"><Button onClick={() => props.changeRoute('/ingame')}>Level {n}</Button></div>)}
                </div>
                <div className="col-md-6">
                    <Button>Shop</Button>
                    <Button>Units</Button>
                </div>

            </div>
        </div>
    );
};

export default connectRedux()(LevelSelect);
