import React from 'react';
import { connectRedux } from '../../common/redux';

const Attack = ({attack, selectAttack, selectedAttack}) => {
    let selectedClass = '';
    if(selectedAttack && selectedAttack.name === attack.name) {
        selectedClass = 'selected-attack';
    }
    return (
        <div className={'attack ' + selectedClass} onClick={() => selectAttack(attack)}>
            {attack.name}
        </div>
    );
};

const Infobar = ({board, selectAttack, undoMovement}) => {
    let unit = board.selectedUnit;
    if(!unit) {
        return <div><h4>{board.currentTurn}</h4></div>;
    }
    return (
        <div>
            <h4>{board.currentTurn}</h4>
            <h3>{unit.name}</h3>
            {unit.finished && <div>Turn Finished</div>}
            <div>Health: {unit.tiles.length}</div>
            <div>Max Health: {unit.maxLength}</div>
            <div>Move: {unit.movesTaken}/{unit.moves}</div>
            <div>attack: {JSON.stringify()}</div>
            {unit.getAttacks().map((attack, index) =>
                <Attack selectedAttack={board.selectedAttack}  key={index} attack={attack} selectAttack={selectAttack} />
            )}
            <div onClick={() => undoMovement()}>Undo</div>
        </div>
    );
};

export default connectRedux(({board})=>({board}))(Infobar);
