import { connect } from 'react-redux';
import { changeRoute } from '../actions/router';
import { boardMouseClick, selectAttack, undoMovement } from '../actions/board';
import _ from 'lodash';

export function connectRedux(mapStateToProps = null, actions = {}) {
    return connect(mapStateToProps, _.merge({}, actions, {
        changeRoute,
        boardMouseClick,
        selectAttack,
        undoMovement,
    }));
}
