import React from 'react';
import { connect } from 'react-redux';
import routes from './routes'

const CustomRouter = (props) => {
    let CurrentRoute = routes[props.route];

    if(CurrentRoute) {
        return <CurrentRoute />;
    } else {
        console.warn(`Route did not match - '${props.route}'`);
        return <span />;
    }
};

export default connect(({route}) => ({route}))(CustomRouter);
