import React from 'react';
import { connectRedux } from '../../common/redux';
import _ from 'lodash';

import MouseLayer from './MouseLayer';
import Layer from './Layer';

const TileBackground = ({tile}) => {
    if(!tile.exists) {
        return null;
    }
    return (
        <rect x={tile.x * 46} y={tile.y * 46} width="40" height="40" style={{fill: 'white', opacity: 0.25}} />
    );
};

const TileLayer = ({tiles}) => {
    return (
        <Layer index={1}>
            {tiles.map((tile, i) => <TileBackground key={i} tile={tile} />)}
        </Layer>
    );
};

const Unit = ({unit}) => {
    if(unit.tiles.length === 0) {
        return null;
    }
    let rects = unit.tiles.map((tile, i) =>
        <rect key={i} x={tile[0] * 46} y={tile[1] * 46} width="40" height="40" fill={unit.color} />
    );
    rects.push(<text key='pic' x={unit.tiles[0][0] * 46} y={unit.tiles[0][1] * 46 + 40} textLength="40">{unit.image}{unit.finished && '✓'}</text>)
    return rects;
};

const UnitLayer = ({units}) => {
    return (
        <Layer index={2}>
            {units.map((unit, i) => <Unit key={i} unit={unit} />)}
        </Layer>
    );
};

const renderMovementIndicators = ({selectedUnit, tiles, units, selectedAttack}) => {
    if(!selectedUnit || !selectedUnit.hasMovementLeft() || selectedAttack) {
        return null;
    }
    let otherUnitsTiles = _.flatMap(units.filter(unit => unit !== selectedUnit), 'tiles');
    let center = selectedUnit.head();

    let indicators = [
        [center[0], center[1] + 1],
        [center[0], center[1] - 1],
        [center[0] + 1, center[1]],
        [center[0] - 1, center[1]],
    ].filter(tile => {
        let found = _.find(tiles, {x: tile[0], y: tile[1]});
        return found && found.exists && _.every(otherUnitsTiles, otherUnitsTile => !_.isEqual(otherUnitsTile, tile));
    });
    return indicators.map((tile, index) => <image key={index} href='assets/movement.png' width='40' height='40' x={tile[0] * 46} y={tile[1] * 46} />);
};

const renderAttackIndicators = ({selectedAttack, selectedUnit, tiles}) => {
    if(!selectedAttack) {
        return null;
    }
    let center = selectedUnit.head();
    let indicators = [
        [center[0], center[1] + 1],
        [center[0], center[1] - 1],
        [center[0] + 1, center[1]],
        [center[0] - 1, center[1]],
    ].filter(tile => {
        let found = _.find(tiles, {x: tile[0], y: tile[1]});
        return found && found.exists && _.every(selectedUnit.tiles, selectedUnitTile => !_.isEqual(selectedUnitTile, tile));
    });
    return indicators.map((tile, index) => <image key={index} href='assets/target.png' width='40' height='40' x={tile[0] * 46} y={tile[1] * 46} />);
}

const EffectsLayer = connectRedux(({board}) => ({board}))(({board}) => {
    return (
        <Layer index={3}>
            {renderMovementIndicators({
                tiles: board.tiles,
                selectedUnit: board.selectedUnit,
                units: _.concat(board.playerUnits, board.cpuUnits),
                selectedAttack: board.selectedAttack,
            })}
            {renderAttackIndicators({
                selectedAttack: board.selectedAttack,
                selectedUnit: board.selectedUnit,
                tiles: board.tiles,
            })}
        </Layer>
    );
});

const GameBoard = ({board}) => {
    return (
        <div>
            <TileLayer tiles={board.tiles} />
            <UnitLayer units={_.concat(board.playerUnits, board.cpuUnits)} />
            <EffectsLayer />
            <MouseLayer />
        </div>
    );
};

export default connectRedux(({board, boardSelection})=>({board, boardSelection}))(GameBoard);
